  .timeline {
    position: relative;
  }
  .timeline::before {
    content: '';
    background: grey;
    width: 5px;
    height: 95%;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }

  .timeline-item {
    width: 100%;
    margin-bottom: 10px;
  }
  .timeline-item:nth-child(even) .timeline-content {
    float: right;
    padding: 40px 30px 10px 30px;
  }
  .timeline-item:nth-child(even) .timeline-content .date {
    right: auto;
    left: 0;
  }
  .timeline-item:nth-child(even) .timeline-content::after {
    content: '';
    position: absolute;
    border-style: solid;
    width: 0;
    height: 0;
    top: 30px;
    left: -15px;
    border-width: 10px 15px 10px 0;
    border-color: transparent black transparent transparent;
  }
  .timeline-item::after {
    content: '';
    display: block;
    clear: both;
  }

  .timeline-content {
    position: relative;
    width: 45%;
    padding: 10px 30px;
    border-radius: 4px;
    background: #f5f5f5;
    box-shadow: 0 20px 25px -15px rgba(0, 0, 0, 0.3);
  }
  .timeline-content::after {
    content: '';
    position: absolute;
    border-style: solid;
    width: 0;
    height: 0;
    top: 30px;
    right: -15px;
    border-width: 10px 0 10px 15px;
    border-color: transparent transparent transparent black;
  }

  .timeline-img {
    width: 30px;
    height: 30px;
    background: black;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    margin-top: 25px;
    margin-left: -15px;
  }

.timeline-item a {
    background: #3F51B5;
    color: #FFFFFF;
    padding: 8px 20px;
    text-transform: uppercase;
    font-size: 14px;
    margin-bottom: 20px;
    margin-top: 10px;
    display: inline-block;
    border-radius: 2px;
    box-shadow: 0 1px 3px -1px rgba(0, 0, 0, 0.6);
  }
.timeline-item a:hover, .timeline a:active, .timeline a:focus {
    background: #32408f;
    color: #FFFFFF;
    text-decoration: none;
  }

  .timeline-card {
    padding: 0 !important;
  }

.timeline-item:nth-child(odd) .timeline-card p {
    padding: 0 20px 20px;
  }

  .timeline-item:nth-child(even) .timeline-card p {
      padding: 0 20px;
    }

  .timeline-card a {
    margin-left: 20px;
  }

  .timeline-item:nth-child(1) .timeline-img-header {
      background: linear-gradient(transparent, rgba(0, 0, 0, 0.4)), url(/static/media/webveloper.468d1e43.jpg) center center no-repeat;
    background-size: cover;
  }
  .timeline-item:nth-child(2) .timeline-img-header {
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.4)), url(/static/media/samsung.6db6c041.jpg) center -40px no-repeat;
    background-size: cover;
  }
  .timeline-item:nth-child(3) .timeline-img-header {
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.4)), url("https://hd.unsplash.com/photo-1471479917193-f00955256257") center center no-repeat;
    background-size: cover;
  }
  .timeline-item:nth-child(4) .timeline-img-header {
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.4)), url("https://hd.unsplash.com/photo-1466840787022-48e0ec048c8a") center center no-repeat;
    background-size: cover;
  }
  .timeline-item:nth-child(10) .timeline-img-header {
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.4)), url("https://hd.unsplash.com/photo-1447639703758-f525f36456bf") center center no-repeat;
    background-size: cover;
  }
  .timeline-item:nth-child(11) .timeline-img-header {
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.4)), url("https://hd.unsplash.com/photo-1469429978400-082eec725ad5") center center no-repeat;
    background-size: cover;
  }

  .timeline-img-header {
    height: 200px;
    position: relative;
    margin-bottom: 20px;
  }
  .timeline-img-header h2 {
    font-family: 'Raleway';
    color: #FFFFFF;
    position: absolute;
    bottom: 5px;
    left: 20px;
  }

  .timeline-img-header h3 {
    font-family: 'Open Sans';
    color: #FFFFFF;
    position: absolute;
    bottom: 50px;
